<template>
  <van-row>
    <div class="dt-headPortrait">
      <van-badge v-if="newMsg"
                 dot
                 style="width:100wh">
        <headPortrait v-if="username.split('、').length > 1"
                      size="large"
                      username="群组"></headPortrait>
        <headPortrait v-else
                      size="large"
                      :username="username"></headPortrait>
      </van-badge>
      <van-badge v-else
                 style="width:100wh">
        <headPortrait v-if="username.split('、').length > 1"
                      size="large"
                      username="群组"></headPortrait>
        <headPortrait v-else
                      size="large"
                      :username="username"></headPortrait>
      </van-badge>
    </div>
    <div class="dt-information">
      <van-row>
        <div class="dt-name">
          <p class="dt-text">{{ username }}</p>
        </div>
        <div class="dt-time">{{ time }}</div>
      </van-row>
      <div class="dt-info">{{ info }}</div>
    </div>
    <div class="dt-card-box" />
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import headPortrait from "@/components/headPortrait/headPortrait";

export default {
  //import引入的组件需要注入到对象中才能使用
  props: ['username', 'info', 'time', 'msgType', 'newMsg'],
  components: {
    headPortrait,
  },
  filters: {
  },
  data () {
    //这里存放数据
    return {};
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    if (this.msgType == 7) {
      this.info = '视频消息'
    } else if (this.msgType == 1) {
      this.info = '图片消息'
    }
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {

  },
  beforeCreate () {
  }, //生命周期 - 创建之前
  beforeMount () {
  }, //生命周期 - 挂载之前
  beforeUpdate () {
  }, //生命周期 - 更新之前
  updated () {
  }, //生命周期 - 更新之后
  beforeDestroy () {
  }, //生命周期 - 销毁之前
  destroyed () {
  }, //生命周期 - 销毁完成
  activated () {
  }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {},
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../styles/dimens';
.card-box {
