}

// 会话之间加线
.dt-card-box {
  margin-top: px2rem(12);
  width: 85%;
  border: px2rem(1) solid #f3f3f3;
  float: right;
}

.dt-headPortrait {
  display: flex;
  justify-content: center;
  float: left;
  height: px2rem(53);
  width: 20%;
}

