<!-- 消息列表页 -->
<template>
  <van-row class="dt-message">
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
      </van-col>
      <van-col span="20">
        <p class="dt-message-name">消息</p>
      </van-col>
      <van-col span="2">
        <van-popover v-model="showPopover"
                     trigger="click"
                     :actions="actions"
                     @select="onSelect"
                     placement="bottom-end"
                     :style="{width:'15px'}">
          <template #reference>
            <van-image fit="contain"
                       class="dt-more"
                       :src="require('../../assets/img/更多.png')" />
          </template>
        </van-popover>
      </van-col>
    </van-row>
    <van-row class="dt-message-content">
      <van-pull-refresh v-model="refreshing"
                        @refresh="onRefresh"
                        style="min-height: 100vh;"
                        success-text="刷新成功"
                        success-duration="400">
        <van-list v-model="loading"
                  :finished="finished"
                  finished-text="没有更多了"
                  :error.sync="error"
                  error-text="请求失败，点击重新加载"
                  @load="onLoad">
          <van-swipe-cell v-for="item in conversationList"
                          :key="item.id">
            <van-row class="dt-card"
                     @click="handleMessageClick(item.id,item.last_msg_id)">
              <messageCard :username="item.name | filtName"
                           :info="item.content"
                           :msgType="item.type"
                           :time="timeFormat(item.send_time)"
                           :newMsg="item.unread>0">
              </messageCard>
            </van-row>
            <template #right>
              <van-button @click="deleteMsg(item)"
                          square
                          type="danger"
                          text="删除" />
            </template>
          </van-swipe-cell>
        </van-list>
      </van-pull-refresh>
    </van-row>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import messageCard from "@/components/messageCard/messageCard"
// 加载会话列表
import {
  getConversations,
  hideDialogue
} from "@/api/message";

export default {
  //import引入的组件需要注入到对象中才能使用
  components: { messageCard },
  filters: {
    filtName (name) {
      if (name.length > 4 && name.slice(0, 4) == "多人聊天") {
        return '多人群组'
      } else {
        return name
      }
    }
  },
  data () {
    //这里存放数据
    return {
      // 会话列表
      conversationList: [],
      // 是否发生错误
      error: false,
      // 是否重新刷新
      refreshing: false,
      // 是否正在加载
      loading: false,
      // 是否完成
      finished: false,
      // 是否显示弹窗
      showPopover: false,
      // 通过 actions 属性来定义菜单选项
      actions: [
        { text: '发起会话', icon: require('@/assets/img/faqihuihua.png') },
      ],
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.$store.commit("bottomNav", 1)
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
  },

  beforeCreate () {
  }, //生命周期 - 创建之前
  beforeMount () {
  }, //生命周期 - 挂载之前
  beforeUpdate () {
  }, //生命周期 - 更新之前
  updated () {
  }, //生命周期 - 更新之后
  beforeDestroy () {
  }, //生命周期 - 销毁之前
  destroyed () {
  }, //生命周期 - 销毁完成
  activated () {
  }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    // 加载列表
    onLoad () {
      if (this.refreshing) {
        this.conversationList = [];
        this.refreshing = false;
      }
      let query = {};
      query.uid = JSON.parse(localStorage.getItem("userInfo")).uid;
      query.pageSize = 10000000;
      getConversations(query).then(res => {
        if (res.code === 0) {
          this.conversationList = res.list;
          // 加载状态结束
          this.loading = false;
          // 数据全部加载完成
          this.finished = true;
        }
      }).catch(() => {
        this.error = true;
      });
    },
    // 下拉刷新
    onRefresh () {
      // 清空列表数据
      this.finished = false;
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    },

    deleteMsg (data) {
      let info = {
        uid: JSON.parse(localStorage.getItem("userInfo")).uid,
        id: data.id
      }
      hideDialogue(info).then(() => {
        this.onLoad()
      })
    },

    // 时间格式化
    timeFormat (dataTime) {
      let time;
      if (!dataTime) {
        // 创建会话没有消息，所以时间为null
        time = '';
      } else {
        // ios无法识别-的时间类型，我们将时间转成类似2021/04/10 19:13:19这种形式，ios和安卓都可以使用
        dataTime = dataTime.replace(new RegExp("-", "gm"), '/');
        // 会话中有时间信息,将时间格式化
        if (new Date().format("yyyy") == new Date(dataTime).format("yyyy")) {
          if (new Date().format("yyyyMMdd") == new Date(dataTime).format("yyyyMMdd")) {
            time = new Date(dataTime).format("hh:mm")
          } else {
            time = new Date(dataTime).format("MM-dd")
          }
        } else {
          time = new Date(dataTime).format("yyyy-MM-dd")
        }
      }
      return time;
    },
    // 进入聊天页面
    handleMessageClick (id, lastId) {
      this.$router.push({ name: 'MessageChat', query: { id: id, lastId: lastId } });
    },
    onSelect (action) {
      if (action.text == "发起会话") {
        this.manyPeople();
      }
    },
    //跳转发起会话页面
    manyPeople () {
      this.$router.push({ name: 'checkAddressBook' })
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../styles/dimens';

.dt-message {
  height: 100%;

  .dt-title {
    height: $headerHeight;
    padding-top: $statusBarHeight;
    background-color: #5870f8;
  }

  .dt-more {
    height: px2rem(18);
    width: px2rem(18);
    float: right;
    text-align: right;
    margin-right: px2rem(17);
  }

  .dt-message-name {
    font-size: px2rem(17);
    font-family: 'SourceHanSansCN-Medium';
    color: #ecedf2;
  }

  .dt-message-content {
    height: 85%;
    overflow-y: auto;

    .dt-msg-item-img {
      height: px2rem(50);
      width: px2rem(50);
    }
  }

  .dt-card {
    margin: px2rem(12) 0;
  }
}
.van-badge__wrapper {
  width: 100%;
}
</style>